import { observer } from 'mobx-react';
import * as React from 'react';
import { Page, VPage } from 'tonva';
import { CustomHeaderTemplate } from 'tools/CustomHeaderTemplate';
import { CHome } from './CHome';
import { observable } from 'mobx';

export class VScanCode extends VPage<CHome> {
    @observable isDis: boolean = true;

    async open(param?: any) {
        this.openPage(this.page);
    }

    media = () => {
        if (navigator.userAgent.indexOf("Html5Plus") > -1) {
            // @ts-ignore 
            window.plusBarcode();
        }
    }

    close = () => {
        this.isDis = false;
        /* if (navigator.userAgent.indexOf("Html5Plus") > -1) {
            // @ts-ignore 
            window.plusBccancel();
        } */
    }

    private page = observer(() => {
        setTimeout(() => {
            this.media();
        }, 200);
        let header = <div className='w-100 text-center text-light'>扫一扫</div>;
        return <div className="w-100 h-100 d-flex flex-column">
            <div className="w-100">
                {CustomHeaderTemplate(header, '', '#555555', '')}
            </div>
            {<button onClick={() => this.close()}>关闭</button>}
            <main className="flex-fill" style={{ display: this.isDis ? 'block' : 'none' }}>
                <div id='bcid' className="w-100 h-100" style={{ background: '#CCCCCC' }}></div>
            </main>
        </div>;
        /* return <Page header={header} right={<></>} className="h-100">
            <div id='bcid' className="w-100 h-100" style={{ background: '#CCCCCC' }}></div>
        </Page >; */
    });
}